import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoTipoMovimentacaoInterna } from '../../../../models/dtos/ger/grupoTipoMovimentacaoInterna';

interface GrupoTipoMovimentacaoInternaEditViewProps extends EditViewProps<GrupoTipoMovimentacaoInterna> {}

const GrupoTipoMovimentacaoInternaEditView: FC<GrupoTipoMovimentacaoInternaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<GrupoTipoMovimentacaoInterna>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Grupo de Tipo de Movimentação Interna">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código."
              type={InputType.NUMBER}
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática', placement: 'bottom' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoTipoMovimentacaoInternaEditView;
